'use client';

import { motion, useMotionValue, useTransform, PanInfo, useAnimate } from 'motion/react';
import { MapPin, Navigation, ChevronUp } from 'lucide-react';
import { Card, Typography, Space, Button, Flex, Badge } from 'antd';
import { CheckCircleFilled } from '@ant-design/icons';
import { useEffect } from 'react';
import { Profile } from '@/utils/mockData';

const { Title, Text } = Typography;

interface SwipeCardProps {
    profile: Profile;
    onSwipe: (direction: 'left' | 'right') => void;
    style?: React.CSSProperties;
    triggerSwipe?: 'left' | 'right' | null;
    onSwipeComplete?: () => void;
}

export function SwipeCard({ profile, onSwipe, style, triggerSwipe, onSwipeComplete }: SwipeCardProps) {
    const x = useMotionValue(0);
    const y = useMotionValue(0);
    const rotate = useTransform(x, [-300, 0, 300], [-30, 0, 30]);
    const opacity = useTransform(x, [-250, -150, 0, 150, 250], [0, 1, 1, 1, 0]);
    const [scope, animate] = useAnimate();

    useEffect(() => {
        if (triggerSwipe) {
            const targetX = triggerSwipe === 'right' ? 400 : -400;
            animate(
                scope.current,
                { x: targetX, opacity: 0, rotate: triggerSwipe === 'right' ? 30 : -30 },
                { duration: 0.4, ease: 'easeOut' }
            ).then(() => {
                onSwipe(triggerSwipe);
                onSwipeComplete?.();
            });
        }
    }, [triggerSwipe, animate, scope, onSwipe, onSwipeComplete]);

    const handleDragEnd = (_: any, info: PanInfo) => {
        const threshold = 150;
        if (Math.abs(info.offset.x) > threshold) {
            const direction = info.offset.x > 0 ? 'right' : 'left';
            const targetX = direction === 'right' ? 400 : -400;
            animate(scope.current, { x: targetX, opacity: 0 }, { duration: 0.3, ease: 'easeOut' }).then(() =>
                onSwipe(direction)
            );
        }
    };

    return (
        <motion.div
            ref={scope}
            style={{
                position: 'absolute',
                width: '100%',
                height: '100%',
                cursor: 'grab',
                x,
                y,
                rotate,
                opacity,
                ...style,
            }}
            drag
            dragConstraints={{ left: 0, right: 0, top: 0, bottom: 0 }}
            dragElastic={0.7}
            whileTap={{ cursor: 'grabbing', scale: 0.97 }}
            onDragEnd={handleDragEnd}
            transition={{ type: 'spring', stiffness: 300, damping: 30 }}
        >
            <Card
                style={{
                    width: '100%',
                    height: '100%',
                    borderRadius: 16,
                    overflow: 'hidden',
                    position: 'relative',
                    boxShadow: '0 8px 24px rgba(0,0,0,0.2)',
                    background: '#000',
                }}
                bodyStyle={{ padding: 0, height: '100%' }}
                bordered={false}
            >
                {/* Recently Active Status */}
                <div
                    style={{
                        position: 'absolute',
                        top: 20,
                        left: 20,
                        background: 'rgba(0, 0, 0, 0.6)',
                        borderRadius: '20px',
                        padding: '6px 12px',
                        display: 'flex',
                        alignItems: 'center',
                        gap: '6px',
                        backdropFilter: 'blur(10px)',
                    }}
                >
                    <div
                        style={{
                            width: '8px',
                            height: '8px',
                            borderRadius: '50%',
                            backgroundColor: '#10b981',
                        }}
                    />
                    <Text style={{ color: 'white', fontSize: '12px', fontWeight: '500' }}>
                        Recently Active
                    </Text>
                </div>

                {/* Profile Image */}
                <div
                    style={{
                        position: 'absolute',
                        inset: 0,
                        backgroundImage: `url(${profile.images[0]})`,
                        backgroundSize: 'cover',
                        backgroundPosition: 'center',
                    }}
                >
                    <div
                        style={{
                            position: 'absolute',
                            inset: 0,
                            background:
                                'linear-gradient(to bottom, rgba(0,0,0,0.2), transparent 50%, rgba(0,0,0,0.75))',
                        }}
                    />
                </div>

                {/* LIKE / NOPE badges */}
                <motion.div
                    style={{
                        position: 'absolute',
                        top: 64,
                        left: 32,
                        border: '4px solid #10b981',
                        color: '#10b981',
                        padding: '10px 24px',
                        borderRadius: 12,
                        transform: 'rotate(-20deg)',
                        backgroundColor: 'rgba(255,255,255,0.9)',
                        boxShadow: '0 4px 12px rgba(0,0,0,0.15)',
                        opacity: useTransform(x, [0, 150], [0, 1]),
                        scale: useTransform(x, [0, 150], [0.8, 1]),
                    }}
                >
                    <Title level={2} style={{ margin: 0 }}>
                        LIKE
                    </Title>
                </motion.div>

                <motion.div
                    style={{
                        position: 'absolute',
                        top: 64,
                        right: 32,
                        border: '4px solid #ef4444',
                        color: '#ef4444',
                        padding: '10px 24px',
                        borderRadius: 12,
                        transform: 'rotate(20deg)',
                        backgroundColor: 'rgba(255,255,255,0.9)',
                        boxShadow: '0 4px 12px rgba(0,0,0,0.15)',
                        opacity: useTransform(x, [-150, 0], [1, 0]),
                        scale: useTransform(x, [-150, 0], [1, 0.8]),
                    }}
                >
                    <Title level={2} style={{ margin: 0 }}>
                        NOPE
                    </Title>
                </motion.div>

                {/* Bottom Info */}
                <div
                    style={{
                        position: 'absolute',
                        bottom: 24,
                        left: 0,
                        right: 0,
                        padding: '0 24px',
                        color: 'white',
                    }}
                >
                    <div style={{ marginBottom: '16px' }}>
                        {/* Name, Age and Verified Badge */}
                        <div style={{ display: 'flex', alignItems: 'center', gap: '8px', marginBottom: '8px' }}>
                            <Title level={2} style={{ color: 'white', margin: 0, fontSize: '28px', fontWeight: '600' }}>
                                {profile.name} {profile.age}
                            </Title>
                            <CheckCircleFilled 
                                style={{ 
                                    color: '#1DA1F2', 
                                    fontSize: '20px',
                                    filter: 'drop-shadow(0 0 4px rgba(29, 161, 242, 0.5))'
                                }} 
                            />
                        </div>
                        
                        {/* Distance */}
                        <div style={{ display: 'flex', alignItems: 'center', gap: '4px' }}>
                            <MapPin size={14} color="rgba(255,255,255,0.8)" />
                            <Text style={{ color: 'rgba(255,255,255,0.8)', fontSize: '16px' }}>
                                {Math.round(profile.distance / 1000)} kilometers away
                            </Text>
                        </div>
                    </div>

                    {/* Info Button */}
                    <div style={{ display: 'flex', justifyContent: 'flex-end' }}>
                        <Button
                            shape="circle"
                            icon={<ChevronUp size={18} color="#10b981" />}
                            style={{
                                backgroundColor: 'rgba(255,255,255,0.9)',
                                backdropFilter: 'blur(6px)',
                                boxShadow: '0 2px 8px rgba(0,0,0,0.15)',
                                width: '40px',
                                height: '40px',
                            }}
                        />
                    </div>
                </div>
            </Card>
        </motion.div>
    );
}
